import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import AddItemInput from './comps/AddItemInput'
import ListItem from './comps/ListItem'
import About from './About'
import {BrowserRouter as Router,Route,Link} from 'react-router-dom'
import { Button } from 'antd';


const TODO_KEY = "todos";

class TodoComponents extends Component {
    constructor(props) {
        super(props);

        let data = localStorage.getItem(TODO_KEY);

        this.state = {
            todos: JSON.parse(data) || [],
            tods:data?JSON.parse(data) : []
        }
    }

    handleAdd = (val) => {
        let todos = this.state.todos
        todos.push(val)

        this.setState({
            todos
        })
        localStorage.setItem(TODO_KEY, JSON.stringify(todos))
    }

    handleDelete = (ind) => {
        // 获取原来的数组
        let todos = this.state.todos
        // 过滤数据
        todos = todos.filter((val, index) => {
            // 返回值为true留下,false删除
            return ind !== index
        })
        // 重新赋值
        this.setState({
            todos: todos
        })
        localStorage.setItem(TODO_KEY, JSON.stringify(todos))
    }


    render() {

        const list = this.state.todos.map((val, index) => {
            return <ListItem key={index} value={val} index={index} onDelete={this.handleDelete}/>
        })

        return (
            <div>
                <Button type="primary">Button</Button>
                <Link to="/about">关于</Link>
                <h2>GTD记事本</h2>
                <p>明日复明日,明日何其多</p>
                <ul>
                    {list}
                </ul>
                <AddItemInput onInputChange={this.handleAdd}/>
            </div>
        )
    }
}


class App extends Component {
    render() {
        return (
            <Router>
            <div>
                <Route path="/" exact={true} component={TodoComponents}></Route>
                <Route path="/about" component={About}></Route>
            </div>
            </Router>
        );
    }
}


ReactDOM.render(
    <App />
    , document.getElementById("root")
)

if (module.hot) {
    module.hot.accept();
}